.link {
  display: inline-block;
  &, &:visited {
    color: $color-primary;
    text-decoration: none;

    font-weight: $semibold;
    letter-spacing: 0.07em;
    font-size: 2rem;
  }

  &--small {
    font-size: 1.2rem;
  }

  &--underline {
    padding-bottom: 1.1rem;
    border-bottom: 1px solid $color-primary;
  }
  &--underline-small {
    padding-bottom: .25rem;
    border-bottom: 1px solid $color-primary;
  }
}